.header{
	padding:25px 10px 0;
	height:140px;
	background:radial-gradient(black 15%, transparent 16%) 0 0, radial-gradient(black 15%, transparent 16%) 8px 8px, radial-gradient(rgba(255, 255, 255, .1) 15%, transparent 20%) 0 1px, radial-gradient(rgba(255, 255, 255, .1) 15%, transparent 20%) 8px 9px;
	background-color:#46474b;
	background-size:16px 16px;
	@include after();
	.wrapper-logo{
		@include dibt();
		margin:-17px 0 0 14px;
	}
	.nav{
		@include dibt();
		> ul{
			@include after();
			> li{
				float:left;
				margin-left:24px;
			}
		}
	}
}
body, html{
	min-height:100%;
	overflow:hidden;
	height:100%;
	background-color:#fdf9ef;
}
.wrapper{
	height:100%;
	position:relative;
	z-index:0;
	overflow-x:hidden;
	overflow-y:auto;
	-webkit-overflow-scrolling:touch;
	position:relative;
}
.main{
	min-height:calc(100% - 280px);
}
.list-categories{
	> li{
		overflow:hidden;
		height:270px;
		position:relative;
		z-index:0;
		@include transition();
		&:hover{
			&:after{
				background-color:rgba(#fff, 0);
			}
		}
		> img{
			@include aa(0);
			width:100%;
		}
		> svg{
			@include aa(0);
		}
		&:after{
			@include transition();
			background-color:rgba(#fff, .5);
			content:'';
			position:absolute;
			z-index:1;
			top:0;
			left:0;
			width:100%;
			height:100%;
		}
		> .wrapper-content-list{
			width:100%;
			height:100%;
			@include aa(2);
			.title{
				margin-top:7px;
				text-align:center;
				> a{
					text-decoration:none;
					border:1px solid #ff5a00;
					border-radius:50%;
					@include dibt();
					@include ar(72px);
					padding:23px 20px 12px;
					background-color:#fff;
					color:#ff5a00;
				}
			}
			.sub-category{
				white-space:nowrap;
				margin-top:15px;
				text-align:center;
				> li{
					white-space:normal;
					position:relative;
					z-index:0;
					@include dibt();
					margin-left:24px;
					&:first-child{
						margin-left:0;
					}
					> .link-text{
						@include transition(.7s);
						display:block;
						text-decoration:none;
						cursor:pointer;
						width:140px;
						margin:9px auto 0;
						@include br(16px);
						color:#ff5a00;
						border:1px solid #ff5a00;
						border-radius:50%;
						padding:8px 20px 4px;
						background-color:#fff;
					}
					&:hover .link-text{
						border:1px solid #fff;
						background-color:#ff5a00;
						color:#fff;
					}
				}
			}
			.controlls-slider{
				> li{
					> a{
						@include dibt();
						position:absolute;
						z-index:1;
						top:50%;
						@include translate(0, -50%);
					}
					&:first-child{
						a{
							left:20px;
						}
					}
					&:nth-child(2){
						a{
							right:20px;
						}
					}
				}
			}
		}
	}
}
.footer{
	padding:25px 10px 0;
	height:140px;
	background:radial-gradient(black 15%, transparent 16%) 0 0, radial-gradient(black 15%, transparent 16%) 8px 8px, radial-gradient(rgba(255, 255, 255, .1) 15%, transparent 20%) 0 1px, radial-gradient(rgba(255, 255, 255, .1) 15%, transparent 20%) 8px 9px;
	background-color:#46474b;
	background-size:16px 16px;
	@include after();
	.copyright{
		float:left;
		color:#fff;
		@include ar(50px);
		margin-left:15px;
		margin-top:28px;
	}
	.contacts{
		text-decoration:none;
		float:right;
		color:#fff;
		@include ar(50px);
		margin-right:15px;
		margin-top:28px;
	}
	.footer-nav{
		margin:0 auto;
		width:600px;
		text-align:center;
	}
}
.wrapper-rotation-drag-menu{
	width:80px;
	margin:0 auto;
	height:80px;
	position:fixed;
	z-index:100;
	left:50%;
	top:100%;
	margin-top:-40px;
	margin-left:-40px;
	.rotation-drag-menu{
		left:-135px;
		top:-135px;
		position:relative;
		z-index:2;
		width:350px;
		height:350px;
		border-radius:50%;
		background-color:#46474b;
		div{
			position:absolute;
			z-index:0;
			left:50%;
			top:50%;
			margin-top:-175px;
			margin-left:-175px;
			color:#fff;
		}
	}
	.show-hide-rdm{
		position:absolute;
		z-index:4;
		width:80px;
		height:80px;
		border-radius:50%;
		left:50%;
		top:50%;
		margin-top:-40px;
		margin-left:-40px;
		span{
			display:block;
			border-radius:50%;
			background-color:#ff5a00;
			width:80px;
			height:80px;
			position:absolute;
			z-index:0;
			top:50%;
			left:50%;
			margin-left:-40px;
			margin-top:-40px;
			svg{
				position:absolute;
				z-index:0;
				top:7px;
				left:23px;
			}
		}
	}
}
@keyframes pulsar{
	0%{
		transform:scale(0);
		opacity:1;
	}
	75%{
		opacity:1;
	}
	100%{
		transform:scale(1);
		opacity:0;
	}
}
@-webkit-keyframes pulsar{
	0%{
		-webkit-transform:scale(0);
		opacity:1;
	}
	75%{
		opacity:1;
	}
	100%{
		-webkit-transform:scale(1);
		opacity:0;
	}
}
.pulsar{
	width:100px;
	height:100px;
	position:absolute;
	z-index:0;
	top:0;
	left:50%;
	margin-left:-50px;
	margin-top:-10px;
}
.pulsar .ring{
	box-sizing:border-box;
	position:absolute;
	top:50%;
	left:50%;
	background:#ff5a00;
	border-radius:50%;
	width:100%;
	height:100%;
	margin-top:-50px;
	margin-left:-50px;
	transform:scale(0);
}
.pulsar .ring:nth-child(1){
	-webkit-animation:pulsar 2s 0s linear infinite;
	animation:pulsar 6s 0s linear infinite;
}
.pulsar .ring:nth-child(2){
	-webkit-animation:pulsar 2s 1s linear infinite;
	animation:pulsar 6s .5s linear infinite;
}
.pulsar .ring:nth-child(3){
	-webkit-animation:pulsar 2s 2s linear infinite;
	animation:pulsar 6s 3s linear infinite;
}
.pulsar .ring:nth-child(4){
	-webkit-animation:pulsar 2s 1.5s linear infinite;
	animation:pulsar 6s 4.5s linear infinite;
}
.up{
	cursor:pointer;
	position:fixed;
	z-index:1000;
	bottom:100px;
	right:50px;
	width:30px;
	height:30px;
	text-indent:-9999em;
	opacity:0;
	@include transition();
	&.show{
		opacity:1;
	}
	> svg{
		position:absolute;
		z-index:0;
		top:-22px;
		left:-25px;
		@include rotate(-90deg);
	}
}
