.controls-rotation-drag-menu{
	width:0;
	height:0;
	position:absolute;
	z-index:3;
	left:50%;
	bottom:0;
	.control{
		width:100px;
		height:100px;
		border-radius:50%;
		display:block;
		position:absolute;
		z-index:0;
		bottom:30px;
		@include scale(.5);
		overflow:hidden;
		&.left{
			left:-287px;
			a{
				@include sprite($left);
			}
		}
		&.right{
			left:185px;
			a{
				@include sprite($right);
			}
		}
		a{
			position:absolute;
			z-index:0;
			left:50%;
			top:50%;
			margin-left:-60px;
			margin-top:-60px;
			border-radius:50%;
			cursor:pointer;
			display:block;
		}
	}
}
